import React from "react";
import { dateFns } from "@faharmony/locale";
import { Button, Text } from "@faharmony/components";
import { Box, SMLType } from "@faharmony/theme";
import { faAngleRight, faAngleLeft, faCalendarAlt } from "@faharmony/icons";

import DayPickerInput from "react-day-picker/DayPickerInput";
import {
  DateUtils,
  DayPickerInputProps,
  DayPickerProps,
  NavbarElementProps,
  //   CaptionElementProps,
} from "react-day-picker";

import { TextField } from "../TextField";
import { StyledCalendar } from "./style";

// Defaults
const className = "DatePicker";
const defaultDateFormat = "yyyy-M-d";
const defaultDisabledDates = [{ daysOfWeek: [0, 6] }];
const generateDisabledDatesFromEnabledDates = (enabledDates: Date[]) => (
  date: Date
) =>
  enabledDates.findIndex((enabledDate) =>
    dateFns.isSameDay(enabledDate, date)
  ) < 0;

export interface IPickerProps {
  // DayPicker
  format?: DayPickerInputProps["format"];
  disabledDates?: DayPickerProps["disabledDays"];
  placeholder?: string;
  value?: Date;
  // Custom
  disabled?: boolean;
  enabledDates?: Date[];
  spacing?: SMLType;
}

const CalendarOverlay = React.forwardRef(
  ({ children, classNames, ...props }: any, ref) => (
    <StyledCalendar {...props} className={classNames.overlayWrapper} ref={ref}>
      {children}
    </StyledCalendar>
  )
);

const NavBar = ({
  onPreviousClick,
  onNextClick,
  month,
  localeUtils,
  previousMonth,
  nextMonth,
  showNextButton,
  showPreviousButton,
}: NavbarElementProps) => {
  const currentMonth = localeUtils.getMonths()[month.getMonth()];
  const currentYear = month.getFullYear().toString();
  const months = localeUtils.getMonths();
  const prev = months[previousMonth.getMonth()];
  const next = months[nextMonth.getMonth()];
  return (
    <Box justifyContent="space-between" className="Calendar-Navigation">
      {showPreviousButton && (
        <Button
          icon={faAngleLeft}
          onClick={() => onPreviousClick()}
          spacing="xs"
          tooltip={prev}
        />
      )}
      <Text
        value={currentMonth + " " + currentYear}
        variant="h5"
        align="center"
      />
      {showNextButton && (
        <Button
          icon={faAngleRight}
          onClick={() => onNextClick()}
          spacing="xs"
          tooltip={next}
        />
      )}
    </Box>
  );
};

export const PickerInput = ({
  disabled = false,
  format = defaultDateFormat,
  disabledDates = defaultDisabledDates,
  placeholder = "YYYY-MM-DD",
  enabledDates = [],
  spacing,
  ...props
}: IPickerProps & DayPickerInputProps) => {
  const dayPickerProps: DayPickerProps = {
    showWeekNumbers: true,
    disabledDays:
      enabledDates.length > 0
        ? generateDisabledDatesFromEnabledDates(enabledDates)
        : disabledDates,
    navbarElement: NavBar,
    firstDayOfWeek: 1,
    captionElement: () => null,
  };
  return (
    <DayPickerInput
      {...props}
      dayPickerProps={dayPickerProps}
      component={React.useCallback(
        React.forwardRef<HTMLInputElement, object>((props, ref) => (
          <TextField
            {...props}
            ref={ref}
            suffixIcon={faCalendarAlt}
            disabled={disabled}
            spacing={spacing}
          />
        )),
        []
      )}
      placeholder={placeholder}
      style={{ width: "100%", ...props.style }}
      classNames={{
        container: className,
        overlayWrapper: className + "-OverlayWrapper",
        overlay: className + "-Overlay",
      }}
      format={format}
      formatDate={dateFns.format as any}
      parseDate={(str, format) => {
        const parsed = dateFns.parse(str, format, new Date());
        return DateUtils.isDate(parsed) ? parsed : undefined;
      }}
      overlayComponent={CalendarOverlay}
    />
  );
};

