/**
 * Web Font Loader gives you added control when using linked fonts via @font-face.
 * It provides a common interface to loading fonts regardless of the source,
 * then adds a standard set of events you may use to control the loading experience.
 * The Web Font Loader is able to load fonts from Google Fonts, Typekit,
 * Fonts.com, and Fontdeck, as well as self-hosted web fonts.
 * It is co-developed by Google and Typekit.
 * @see https://github.com/typekit/webfontloader/blob/master/README.md
 *
 * The font CSS shorthand property sets all the different properties of an element's font.
 * Alternatively, it sets an element's font to a system font.
 * @see https://developer.mozilla.org/en-US/docs/Web/CSS/font
 */
import { IThemeCode, IThemeText } from "../types";

// Defaults
export const baseFontFamily = "Mulish";
export const baseFontSize = 14;
export const convertPxToRem = (px: number = 0) =>
  px === 0 ? "1rem" : px / baseFontSize + "rem";

/** Code variant of Text */
export const code: Readonly<IThemeCode> = {
  fontConfig: {
    google: {
      families: ["Fira Code"],
    },
  },
  style: {
    fontSize: convertPxToRem(12),
    fontWeight: "normal",
    lineHeight: convertPxToRem(20),
    letterSpacing: 0,
    textTransform: "none",
  },
};

/** All text-styles except code */
export const text: Readonly<IThemeText> = {
  fontConfig: {
    google: {
      families: [`${baseFontFamily}:400,700,800,900`],
    },
  },
  h1: {
    fontSize: convertPxToRem(26), //
    fontWeight: 800,
    lineHeight: convertPxToRem(36),
    letterSpacing: 0,
    textTransform: "capitalize",
  },
  h2: {
    fontSize: convertPxToRem(22),
    fontWeight: 700,
    lineHeight: convertPxToRem(32),
    letterSpacing: 0,
    textTransform: "none",
  },
  h3: {
    fontSize: convertPxToRem(18),
    fontWeight: 700,
    lineHeight: convertPxToRem(24),
    letterSpacing: 0,
    textTransform: "none",
  },
  h4: {
    fontSize: convertPxToRem(16),
    fontWeight: 700,
    lineHeight: convertPxToRem(24),
    letterSpacing: 0,
    textTransform: "none",
  },
  h5: {
    fontSize: convertPxToRem(baseFontSize),
    fontWeight: 700,
    lineHeight: convertPxToRem(20),
    letterSpacing: "0.5px",
    textTransform: "none",
  },
  h6: {
    fontSize: convertPxToRem(12),
    fontWeight: 700,
    lineHeight: convertPxToRem(16),
    letterSpacing: "0.25px",
    textTransform: "none",
  },
  strong: {
    fontSize: convertPxToRem(12),
    fontWeight: 900,
    lineHeight: convertPxToRem(16),
    letterSpacing: "1px",
    textTransform: "uppercase",
  },
  p: {
    fontSize: convertPxToRem(baseFontSize),
    fontWeight: 400,
    lineHeight: convertPxToRem(20),
    letterSpacing: 0,
    textTransform: "none",
  },
  small: {
    fontSize: convertPxToRem(12),
    fontWeight: 400,
    lineHeight: convertPxToRem(16),
    letterSpacing: 0,
    textTransform: "none",
  },
};
