import React from "react";
import { Box, theme, styled } from "@faharmony/theme";
import {
  Icon,
  Tooltip,
  Button,
  Popover,
  IPopoverMenuItemProps,
} from "@faharmony/components";
import { openExternalLink } from "@faharmony/router";
import { faExternalLinkSquare, getIconDefinition } from "@faharmony/icons";
import { IFABarProps, IFABarAppButtonProps } from "./types";
import { isAppActive } from "./common";
import FALogo from "./FALogo";

const barWidth = "52px";
const appButtonHeight = "36px";

const { color, shadow, spacing, zIndex, radius } = theme;

const StyledBar = styled.aside`
  position: relative;
  z-index: ${zIndex.docked};
  margin: 0;
  box-shadow: ${shadow.large};
  width: ${barWidth};
  max-width: ${barWidth};
  height: 100%;
  max-height: 100vh;
  background-color: ${color.background.default};
  display: grid;
  grid-template-rows: ${barWidth} minmax(auto, 1fr) max-content;
  gap: ${spacing.sm};

  #FA-Bar-apps {
    margin-top: 16px;
    padding-top: 1px;
  }

  #FA-Bar-actions {
    padding-bottom: ${spacing.md};
  }
`;

const StyledAppButton = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-size: 1.75rem;
  cursor: pointer;
`;

const StyledAppButtonInactive = styled(StyledAppButton)`
  height: ${appButtonHeight};
  width: ${appButtonHeight};
  box-shadow: ${shadow.small};
  border-radius: ${radius[300]};
  background: ${color.background.primary};
  color: ${color.text.secondary};
  :hover {
    color: ${color.text.primary};
    box-shadow: ${shadow.default};
  }
`;

const StyledAppButtonActive = styled(StyledAppButton)`
  height: calc(${appButtonHeight} + ${spacing.md});
  width: calc(${barWidth} - ${spacing.xs});
  border-radius: ${radius[500]} 0 0 ${radius[500]};
  background: ${color.background.disabled};
  margin-left: ${spacing.xs};
  padding-right: ${spacing.xs};
  color: ${color.static.accent};
  box-shadow: ${shadow.inner};
`;

const AppButton = (app: IFABarAppButtonProps) => {
  const { appName, iconPath, url, openInNewWindow, id } = app;

  const ButtonWrapper = isAppActive(app)
    ? StyledAppButtonActive
    : StyledAppButtonInactive;
  const appButtonMenuItems: IPopoverMenuItemProps[] = [
    {
      id: "openSameWindow",
      label: "Open app",
      onClick: () => openExternalLink(url, "_self"),
    },
    {
      id: "openNewWindow",
      label: "Open app in new tab",
      onClick: () => openExternalLink(url, "_blank"),
      icon: faExternalLinkSquare,
    },
  ];
  return (
    <Popover.Menu menuItems={appButtonMenuItems} trigger="contextmenu">
      <span tabIndex={0}>
        <Tooltip value={appName} placement="right">
          <ButtonWrapper
            onClick={() =>
              openExternalLink(url, openInNewWindow ? "_blank" : "_self")
            }
            id={"faGlobalBarApp-" + id}
          >
            <Icon icon={getIconDefinition(iconPath)} size="1x" />
          </ButtonWrapper>
        </Tooltip>
      </span>
    </Popover.Menu>
  );
};

const VerticalBar = ({ actions, apps }: IFABarProps) => {
  return (
    <StyledBar id="faGlobalBar">
      <FALogo />
      <Box
        id={"FA-Bar-apps"}
        direction={"column"}
        justifyContent="flex-start"
        alignItems="center"
        style={{ overflowX: "hidden", overflowY: "auto" }}
      >
        {apps.map((app) => (
          <AppButton key={app.id} {...app} />
        ))}
      </Box>
      <Box
        id={"FA-Bar-actions"}
        direction={"column"}
        justifyContent="flex-end"
        alignItems="center"
      >
        {actions.map((action) => (
          <Button
            key={action.id}
            {...action}
            variant="secondary"
            id={"faGlobalBarAction-" + action.id}
          />
        ))}
      </Box>
    </StyledBar>
  );
};

export default VerticalBar;
