/** Export `theme` object */
import { themeLight as faTheme } from "./faTheme";
import { ITheme } from "./types";
import { IThemeColor, IThemeShadow } from "./types";

/** Variable based color object */
const color: IThemeColor = Object.keys(faTheme.color).reduce(
  (colors, type) => ({
    ...colors,
    [type]: Object.keys(faTheme.color[type]).reduce(
      (result, item) => ({
        ...result,
        [item]: `var(--color-${type}-${item})`,
      }),
      {}
    ),
  }),
  {} as IThemeColor
);

/** Variable based shadow object */
const shadow: IThemeShadow = Object.keys(faTheme.shadow).reduce(
  (result, item) => {
    result[item] = `var(--shadow-${item})`;
    return result;
  },
  {} as Record<keyof IThemeShadow, string>
);

/** Theme object usable in code */
export const theme: ITheme = {
  ...faTheme,
  color,
  shadow,
};
