import { ITheme, ITextStyle } from "../types";
import { getFontName, isTextStyle } from "../helpers";

/** Generate CSS string for color variables */
export const generateColorAndShadowCSS = (
  { color, shadow }: ITheme,
  enableDarkMode: boolean
) => {
  // cssString contains the variables-value pairs
  let cssString = `:root {`;
  // Set DarkMode preference variable
  cssString += `--darkmode: ${enableDarkMode.toString()};`;
  // Colors
  for (const [key1, value1] of Object.entries({ ...color }))
    for (const [key2, value2] of Object.entries({ ...value1 }))
      cssString += ` --color-${key1}-${key2}:${value2}; `;
  // Shadows
  for (const [key, value] of Object.entries({ ...shadow }))
    cssString += ` --shadow-${key}:${value}; `;
  // Close ROOT
  cssString += `} `;
  return cssString;
};

/** Generate CSS string for font variables  */
const generateFontCSS = ({ text, code }: ITheme) => {
  const fontCSSString = (name: string, style: ITextStyle) => `{       
      font-family: ${name};
      font-style: normal;
      font-size: ${style.fontSize}; 
      font-weight: ${style.fontWeight}; 
      line-height: ${style.lineHeight}; 
      letter-spacing: ${style.letterSpacing}; 
      text-transform: ${style.textTransform}; 
      margin: 0;
      vertical-align: bottom;
      white-space: wrap;
      display: inline;
    }`;

  let cssString = "";
  cssString += `code, .code ${fontCSSString(getFontName(code), code.style)}`;
  Object.entries(text).forEach(([key, val]) => {
    if (isTextStyle(val))
      cssString += `${key}, .${key} ${fontCSSString(getFontName(text), val)}`;
  });
  return cssString;
};

/** Generate THEME CSS */
export const generateCSS = (
  theme: ITheme,
  enableDarkMode: boolean,
  globalCss: string = ""
) => {
  const { color, size, radius, text, code } = theme;
  const textFontName = getFontName(text);
  const codeFontName = getFontName(code);
  const cssString = `
*,
*:before,
*:after {  
  box-sizing: border-box;
  scrollbar-color: var(--themeMode);
  scrollbar-width: thin;
  padding: 0;
  margin: 0;
}

${generateColorAndShadowCSS(theme, enableDarkMode)}

/* width */
*::-webkit-scrollbar,
*::-webkit-scrollbar:horizontal,
*::-webkit-scrollbar:vertical {
  width: ${size[200]};
  height: ${size[200]};
}

/* Track & corner */
*::-webkit-scrollbar-track,
*::-webkit-scrollbar-corner {
  /* box-shadow: inset 0 0 5px grey; */
  background: ${color.background.hover};
}

/* Handle */
*::-webkit-scrollbar-thumb {
  background: ${color.text.disabled};
  border-radius: 10px;
}

/* Handle on hover */
*::-webkit-scrollbar-thumb:hover {
  background: ${color.border.selected};
}



*:focus {
  outline: none !important;
}

html,
body {
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
  font-family: ${textFontName}, sans-serif;
  font-size: 14px;
  color: ${color.text.primary};
  overflow: hidden;
}

.noselect {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.stretchHeight {
  height: 100%;
  height: -moz-available;          /* Mozilla */
  height: -webkit-fill-available;  /* Safari, Chrome */
  height: stretch;
}

pre {
  font-family: ${codeFontName}, monospace; 
  background: ${color.background.disabled};
  padding: ${size[300]};
  border-radius: ${radius[300]};
  overflow-x: auto;
}        


code, .code {
  background: var(--colors-background-disabled);
  padding: 0 2px;
  margin: 0 2px;
}

a, a:link, a:visited, a:active {
  color: ${color.text.info};
}

input {
  font-family: Mulish, sans-serif;
  font-size: 14px;
  color: ${color.text.primary};
}

blockquote {
  font-style: italic !important;
  opacity: 0.9 !important;
}


// Tippy
.tippy-iOS {
  cursor: pointer !important;
  -webkit-tap-highlight-color: transparent;
}

${generateFontCSS(theme)}

${globalCss}`;

  // Place it in DOM
  const styleTagID = "style-theme";
  const existingStyleTag = document.getElementById(
    styleTagID
  ) as HTMLStyleElement | null;

  const newStyleTag = document.createElement("style");
  newStyleTag.id = styleTagID;

  const styleTag: HTMLStyleElement = existingStyleTag || newStyleTag;
  styleTag.innerHTML = cssString;

  if (!existingStyleTag)
    document.head.insertAdjacentElement("beforeend", styleTag);
};
