import { styled, theme } from "@faharmony/theme";
import { IMenuPrimaryItemProps, IMenuSecondaryItemProps } from "../types";

interface StyleProps {
  isMenuCollapsed: boolean;
  isHeaderShown: boolean;
}

export const StyledNavigationMenu = styled.nav<StyleProps>`
  position: relative;
  width: inherit;
  height: inherit;
  background: transparent;
  padding-top: ${theme.spacing.md};
  padding-bottom: ${theme.spacing.md};
  overflow: auto;
  ::-webkit-scrollbar,
  ::-webkit-scrollbar:horizontal,
  ::-webkit-scrollbar:vertical {
    width: ${theme.spacing.xs};
    height: ${theme.spacing.xs};
  }

  ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
  }

  .noContent {
    color: ${() => theme.color.text.disabled};
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
`;

const iconWidth = "24px";

const NavItemCommonStyle = (
  props: IMenuPrimaryItemProps | IMenuSecondaryItemProps
) => `     
  display: grid;
  grid-gap: ${theme.spacing.md};
  padding: 0 ${theme.spacing.md};
  align-items: center;
  background: ${
    props.isSelected ? theme.color.background.selected : "transparent"
  };
  color: ${
    props.isSelected ? theme.color.text.primary : theme.color.text.secondary
  };
  grid-template-columns: ${
    props.showLeadingIcon ? `${iconWidth} auto max-content` : "auto max-content"
  };
  cursor: pointer;
  border-left: 2px solid ${
    props.isSelected ? theme.color.static.accent : "transparent"
  };

  .Text {
    cursor: pointer;
    user-select: none;
  }
`;

const IconWrapperStyle = () => `.iconWrapper {
  width: ${iconWidth};
  .Icon {
    margin-top: calc(-1 * ${() => theme.spacing.xs});
  }
}`;

const HoverStyle = (props: IMenuPrimaryItemProps | IMenuSecondaryItemProps) => `
  :hover {
    background: ${
      props.isSelected
        ? theme.color.background.selected
        : theme.color.background.hover
    };
    color: ${theme.color.text.primary};
  }
`;

export const StyledNavPrimaryItem = styled.li<IMenuPrimaryItemProps>`
  min-height: 40px;
  margin-bottom: ${theme.spacing.md};
  background: ${(props) =>
    props.isExpanded ? theme.color.background.default : "transparent"};
  padding-bottom: ${(props) => (props.isExpanded ? theme.spacing.sm : 0)};

  .navPrimaryItem {
    min-height: 40px;
    ${NavItemCommonStyle};
    ${IconWrapperStyle};
    ${HoverStyle};
  }
`;

export const StyledNavSecondaryItem = styled.li<IMenuSecondaryItemProps>`
  min-height: 32px;
  ${NavItemCommonStyle};
  ${IconWrapperStyle};
  ${HoverStyle};
`;

export default StyledNavigationMenu;
