import React from "react";
import { KeycloakInstance } from "keycloak-js";
import {
  ApolloProvider,
  ApolloClient,
  ApolloLink,
  createHttpLink,
  InMemoryCache,
} from "@apollo/client";
import { setContext } from "@apollo/client/link/context";
import { getAuthToken } from "../keycloak";

export interface ICustomGraphQL {
  /** Apollo link for custom GraphQL endpoint */
  link: string | ApolloLink;
  /** Use custom GraphQL link as a secondary endpoint */
  split?: boolean;
}

const GraphQLProvider = (props: {
  children: React.ReactNode;
  keycloakInstance: KeycloakInstance;
  customGraphQL?: ICustomGraphQL;
}) => {
  const { keycloakInstance, customGraphQL, children } = props;

  // Auth
  const authLink = setContext((_, { headers }) => {
    // get the authentication token from local storage if it exists
    const token = keycloakInstance?.token || getAuthToken();
    // return the headers to the context so httpLink can read them
    return {
      headers: {
        ...headers,
        authorization: token ? `Bearer ${token}` : "",
        Accept: "application/json",
      },
    };
  });

  // GraphQL
  const defaultLink = createHttpLink({ uri: "/graphql" });

  // Custom
  const customLink = customGraphQL
    ? typeof customGraphQL.link === "string"
      ? customGraphQL.link.length > 0
        ? createHttpLink({ uri: customGraphQL.link })
        : undefined
      : customGraphQL.link
    : undefined;

  // Link
  const link: ApolloLink = customLink
    ? customGraphQL?.split
      ? authLink.split(
          (operation) => operation.getContext().custom === true,
          customLink,
          defaultLink
        )
      : ApolloLink.from([authLink, customLink])
    : ApolloLink.from([authLink, defaultLink]);

  // Cache
  const cache = new InMemoryCache();

  // Client
  const faGraphQlClient = new ApolloClient<any>({ cache, link });

  return <ApolloProvider client={faGraphQlClient}>{children}</ApolloProvider>;
};

export { GraphQLProvider };

