import { ITheme } from "../types";
import { colorLight, colorDark } from "./color";
import { shadowLight, shadowDark } from "./shadow";
import { code, text } from "./font";
import { radius, size, transition, zIndex, spacing } from "./misc";

const themeCommon = { radius, size, code, text, zIndex, transition, spacing };

export const themeLight: Readonly<ITheme> = {
  color: colorLight,
  shadow: shadowLight,
  ...themeCommon,
};

export const themeDark: Readonly<ITheme> = {
  color: colorDark,
  shadow: shadowDark,
  ...themeCommon,
};
