import { StylesConfig, Theme as ThemeConfig } from "react-select";
import { theme, SMLType } from "@faharmony/theme";
import { inputHeight, calcHeight } from "../common";

const { color, shadow, zIndex } = theme;

/**
 * Theme default colors
danger: "#DE350B",
dangerLight: "#FFBDAD",
neutral0: "hsl(0, 0%, 100%)",
neutral10: "hsl(0, 0%, 90%)",
neutral20: "hsl(0, 0%, 80%)",
neutral30: "hsl(0, 0%, 70%)",
neutral40: "hsl(0, 0%, 60%)",
neutral5: "hsl(0, 0%, 95%)",
neutral50: "hsl(0, 0%, 50%)",
neutral60: "hsl(0, 0%, 40%)",
neutral70: "hsl(0, 0%, 30%)",
neutral80: "hsl(0, 0%, 20%)",
neutral90: "hsl(0, 0%, 10%)",
primary: "#2684FF",
primary25: "#DEEBFF",
primary50: "#B2D4FF",
primary75: "#4C9AFF"
 */
export const selectTheme = (
  theme: ThemeConfig,
  spacing: SMLType = "md"
): ThemeConfig => {
  return {
    ...theme,
    spacing: {
      ...theme.spacing,
      baseUnit: 2,
      controlHeight: calcHeight({ spacing }),
      menuGutter: 4,
    },
    colors: {
      ...theme.colors,
      danger: color.text.negative,
      dangerLight: "transparent",
      neutral0: "hsl(0, 0%, 100%)",
      neutral10: "hsl(0, 0%, 90%)",
      neutral20: "hsl(0, 0%, 80%)",
      neutral30: "hsl(0, 0%, 70%)",
      neutral40: "hsl(0, 0%, 60%)",
      neutral5: "hsl(0, 0%, 95%)",
      neutral50: "hsl(0, 0%, 50%)",
      neutral60: "hsl(0, 0%, 40%)",
      neutral70: "hsl(0, 0%, 30%)",
      neutral80: "hsl(0, 0%, 20%)",
      neutral90: "hsl(0, 0%, 10%)",
      primary: color.text.primary,
      primary25: color.background.default,
      primary50: color.text.disabled,
      primary75: color.text.secondary,
    },
  };
};

/** b=baseStyle : CSSProperties
 * s=state : {
    clearValue: function clearValue()
    cx: function classNames();
    getStyles: function getStyles(key, props)
    getValue: function getValue();
    hasValue: true;
    innerProps: Object { onMouseDown: onDropdownIndicatorMouseDown(event), onTouchEnd: onDropdownIndicatorTouchEnd(event), "aria-hidden": "true" };
    isDisabled: false;
    isFocused: false;
    isMulti: false;
    isRtl: false;
    options: Array [ {}, {} ];
    selectOption: function selectOption(newValue);
    selectProps: Object { className: "Combobox", isClearable: true, isSearchable: true,  };
    setValue: function setValue(newValue, action, option);
    theme: Object { borderRadius: 4, colors: {}, spacing: {} };
 * }
 */
export const selectStyles: StylesConfig = {
  clearIndicator: (b) => ({ ...b }),
  container: (b) => ({ ...b, width: "100%" }),
  control: (b, s) => ({
    ...b,
    backgroundColor:
      color.background[
        s.isDisabled
          ? "disabled"
          : s.selectProps.isSearchable
          ? "input"
          : "default"
      ],
    width: "100%",
    boxShadow: s.isFocused
      ? shadow.hover
      : s.selectProps.isSearchable
      ? shadow.inner
      : shadow.default,
    borderColor: color.border.default,
    transition: `border-color 0.5s`,
    "&:hover": { borderColor: color.border.selected },
  }),
  dropdownIndicator: (b) => ({ ...b }),
  group: (b) => ({ ...b }),
  groupHeading: (b) => ({ ...b }),
  indicatorsContainer: (b) => ({ ...b, padding: 0 }),
  indicatorSeparator: (b) => ({ ...b, backgroundColor: color.border.default }),
  input: (b) => ({
    ...b,
    color: color.text.primary,
    fontStyle: "normal",
  }),
  loadingIndicator: (b) => ({ ...b }),
  loadingMessage: (b) => ({ ...b }),
  menu: (b) => ({
    ...b,
    zIndex: zIndex.contextMenu,
    backgroundColor: color.background.primary,
    border: `1px solid ${color.border.default}`,
    boxShadow: shadow.float,
  }),
  menuList: (b) => ({ ...b }),
  menuPortal: (b) => ({ ...b, zIndex: zIndex.contextMenu }),
  // multiValue: (b, s) =>
  //   s.data?.isFixed ? { ...b, backgroundColor: colorBackground("inverse") } : b,
  multiValue: (b) => b,
  multiValueLabel: (b, s) => (s.data?.isFixed ? { ...b, paddingRight: 6 } : b),
  multiValueRemove: (b, s) =>
    s.data?.isFixed
      ? { ...b, display: "none" }
      : { ...b, color: theme.color.static.black },
  noOptionsMessage: (b) => ({ ...b, color: color.text.disabled }),
  option: (b, s) => ({
    ...b,
    color:
      color.text[
        s.isDisabled ? "disabled" : s.isSelected ? "primary" : "secondary"
      ],
    cursor: s.isDisabled ? "inherit" : "pointer",
    backgroundColor: s.isFocused
      ? color.background.disabled
      : s.isSelected
      ? color.background.selected
      : "transparent",
    "&:hover": {
      backgroundColor: s.isDisabled ? "none" : color.background.hover,
    },
  }),
  placeholder: (b) => ({ ...b, color: color.text.disabled }),
  singleValue: (b, s) => ({
    ...b,
    color: color.text[s.isDisabled ? "disabled" : "primary"],
  }),
  valueContainer: (b) => ({
    ...b,
    height: inputHeight,
    padding: "0 4px",
  }),
};
